import React from 'react';
import './Converter.css';

  export class Converter extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        value: '',
        finalText:''
      };
  
      this.handleChange = this.handleChange.bind(this);
      this.handleFinalChange = this.handleFinalChange.bind(this);
    }
  
    handleChange(event) {
      this.setState({value: event.target.value});
    }

    handleFinalChange(event) {
        this.setState({finalText: event.target.value});
      }

    handleClick(text) {
        let lines = text.split("\n");
        const isEmptyOrDataLine = (str) => { return (!str || 0 === str.length); } 

        let dateEntry = ''
        let dateLine = '';
        let startline = ''
        let endline = '';
        const adjustments = [];

        const attendees = lines.map((line) => {
            if (line.includes('---------------------------')){
                dateEntry = line.replace('---------------------------', '[ANONYMOUS] ');
                dateLine = line;
            }
            else if(line.trim().includes('Players on EverQuest:')){
                startline = line;
            } 
            else if (line.includes('There are ')) {
                endline = line;
            }
            else if (line.startsWith('+') || line.startsWith('-') || line.includes('=')) {
                adjustments.push(line)
            }
            else{
                let finalLine = line.replace(/\s*LFG\s*/g, '').replace(/\s*AFK\s*/g, '').replace(/\s*\(.*?\)\s*/g, '').replace(/\s*\[.*?\]\s*/g, '').replace(/\s*\<.*?\>\s*/g, '')
                if(!isEmptyOrDataLine(finalLine)){
                   return dateEntry + finalLine;
                }
            }
        })

        var filteredAttendees = attendees.filter(function (el) {
            return el != null;
          });

        adjustments.map((adjLine) => {
            if (adjLine.startsWith('+')) {
                if(adjLine.includes(',')){
                    let add = adjLine.replace('+', '').split(',')[0];
                    const index = filteredAttendees.findIndex((line) => line.includes(add));
                    if(index == -1){
                        filteredAttendees.push(dateEntry + add);
                    }
                }
                if(adjLine.includes('=')){
                    let add = adjLine.replace('+', '');
                    const replaceArray = add.split('=')
                    const replacee = replaceArray[0];
                    const replaced = replaceArray[1].replace('(', '').split(')')[0]
                    const index = filteredAttendees.findIndex((line) => line.includes(replaced));
                    if(index != -1){
                        filteredAttendees.splice(index, 1);
                        filteredAttendees.push(dateEntry + replacee);
                    }
                }
            }
            if (adjLine.startsWith('-')) {
                let remove = adjLine.replace('-', '').split(',')[0];
                const index = filteredAttendees.findIndex((line) => line.includes(remove));
                if(index != -1){
                    filteredAttendees.splice(index, 1);
                }
                
            }
          })

          filteredAttendees.unshift(dateLine);
          filteredAttendees.unshift(startline);
          filteredAttendees.push(endline);

        if(dateLine === '' || startline === '' || endline === ''){
            this.setState({finalText: 'invalid formatting'});
        }
        this.setState({finalText: filteredAttendees.join('\n')});
    }
  
    render() {
      return (
        <div className="container"  data-tid="container">
        <div>
          <textarea className="textbox" rows={50} cols={80} value={this.state.value} placeholder={"Insert Data here"} onChange={this.handleChange} />
        </div>
        <div className="center" >
          <button
            className="btn"
            onClick={() => this.handleClick(this.state.value)}
            data-tclass="btn"
            type="button"
          >
            <i className="fa fa-arrow-right" />
          </button>
        </div>
        <div>
        <textarea className="textbox" rows={50} cols={80} value={this.state.finalText} onChange={this.handleFinalChange} />
        </div>
      </div>
      );
    }
  }
